.ads-banner {
    padding: 140px 0px;

    @media (max-width: 1125px) {
        padding: 70px 0px;
    }

    @media (max-width: 767px) {
        padding: 0px 0px;
    }

    &__body {
        font-family: "Jost", sans-serif;
        color: #ffffff;
        background-color: #acdc23;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        align-items: center;
        gap: 30px;

        @media (max-width: 1125px) {
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 120px 1fr;
            grid-template-areas: "second second" "first third";
        }
        @media (max-width: 767px) {
            grid-template-columns: 1fr;
            grid-template-rows: 120px 120px 224px;
            grid-template-areas: "first" "second" "third";
            text-align: center;
        }
    }
    &__column {
        &:nth-child(1) {
            padding: 60px 0px 60px 75px;
        }
        &:nth-child(2) {
            padding: 60px 0px 60px 0px;
        }

        @media (max-width: 1125px) {
            &:nth-child(1) {
                padding: 0px;
                grid-area: first;
            }
            &:nth-child(2) {
                padding: 0px;
                grid-area: second;
                text-align: center;
            }
            &:nth-child(3) {
                padding: 0px;
                grid-area: third;
            }
        }
    }

    &__subtitle {
        font-size: 60px;
        span {
            color: #fffc01;
        }
    }
    &__button {
        button {
            line-height: 54px;
            font-size: 21px;
            background-color: rgba($color: #000000, $alpha: 0);
            text-align: left;
            background: url("../../../../assets/images/adsBanner/rightArrow.png") 75% center no-repeat;
        }
        @media (max-width: 767px) {
            button {
                background-position: 100%;
                text-align: center;
            }
        }
    }

    &__title {
        font-size: 46px;
    }
    &__image {
        width: 100%;
        height: 100%;
        img {
            width: 100%;
            height: 100%;
        }
        @media (max-width: 767px) {
            display: grid;
            align-items: center;
            justify-content: end;
            img {
                max-width: 100%;
            }
        }
    }
}
